import React from 'react'
import classes from './Repositories.css'

const repositories = props => {
  const repos = props.repos.map(repo => {
    return (
      <div
        className={classes.Repo}
        key={repo.id}>
        <ul>
          <li>Repository <a href={repo.html_url}>URL</a> </li>
          <li>Repository name: {repo.name}</li>
          <li>Description: {repo.description}</li>
          <li>Language: {repo.language}</li>
        </ul>
      </div>
    )
  })
  return (
    <div className={classes.Repos}>
      <div className={classes.Description}>{
        props.repos.length
          ? `Loaded repos for user: ${props.selectedUser}`
          : 'User wasn\'t selected!'
      }
      </div>
      {repos}
    </div>
  )
}

export default repositories



