import React, { Component } from 'react';
import classes from './App.css';
import { Route, Redirect, Switch } from 'react-router';
import Repositories from './components/Repositories/Repositories';
import Select from './components/Select/Select';
import axios from 'axios'
import { Link } from 'react-router-dom';


class App extends Component {
  state = {
    loadedUsers: [],
    loadedRepos: [],
    selectedUser: null,
    inputValue: ''
  }
  
  findUserHandler = event => {
    this.setState({
      inputValue: event.target.value
    })
    if (event.target.value.trim()) {
      axios.get('https://api.github.com/search/users?q=' + event.target.value)
        .then(res => {
          this.setState({
            loadedUsers: res.data.items.slice(0, 20)
          })
        })
        .catch(error => {
          console.log(error)
        })
    } else {
      this.setState({
        loadedUsers: []
      })
    }
  }

  selectUserHandler = userLogin => {
    this.setState({
      inputValue: userLogin,
      selectedUser: userLogin
    })
    axios.get('https://api.github.com/users/' + userLogin + '/repos')
      .then(res => {
        this.setState({
          loadedRepos: res.data
        })
      })
      .catch(error => {
        console.log(error)
      })
  }

  render() {
    return (
      <div className={classes.App}>
        <div className={classes.Header}>
          <Link className={classes.Link} to='/'>Home</Link>
          <Link className={classes.Link} to='/repos'>Repos</Link>
        </div>
        <Switch>
          <Route path='/repos' render={(props) =>
            <Repositories
              {...props}
              repos={this.state.loadedRepos}
              selectedUser={this.state.selectedUser}
            />}
          />
          <Route path='/' exact render={(props) =>
            <Select
              {...props}
              users={this.state.loadedUsers}
              value={this.state.inputValue}
              change={((event) => this.findUserHandler(event))}
              click={(userLogin) => this.selectUserHandler(userLogin)}
            />}
          />
          <Redirect to='/' />
        </Switch>
      </div >
    );
  }
}

export default App;



